import React from 'react';
import Mybutton from '../components/Mybutton';
import Mybutton2 from '../components/Mybutton2';
import Mytitle from '../components/Mytitle';
import Description from '../components/Description';
import Image from '../components/Image';
import { Card, CardText, CardBody, CardTitle, CardSubtitle } from 'reactstrap'; 
import Mysubtitle from './Mysubtitle';

export default class Mycomponent extends React.Component {
    constructor() {
        super();
        this.state = {
            image: 'https://tagdiv.com/wp-content/uploads/2019/01/tcatalin.jpg',
            text: 'Lungan Catalin-Nicolae'
        }
    }
    changeMytitle = () => {
        this.setState({
            text: 'Mr. Bean'
        });
    }

    changeMyimage = () => {
        this.setState({
            image: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQ3jjNz_Ny1nhURNZTxTyFZVfSVH30L8GQjng&usqp=CAU'
        });
    }
    render(){
         console.log(this.state.image);
        return(
                        
        <div className="wrapper" style={{display:"flex", justifyContent:"center", alignItems:"center", height: "80vh"}}>
            <div className="box" style={{width: '28rem'}}>
                <Card>
                    <Image  image={this.state.image}/>
                    <CardBody>
                        <CardSubtitle tag="h6" className="mb-2 text-muted">
                            <Mysubtitle subtitle={"DEVELOPER"}/>
                        </CardSubtitle>
                        <CardTitle tag="h5"> 
                            <Mytitle text={this.state.text}/> 
                        </CardTitle>
                        <CardText className="text-justify" style={{marginBottom:"10px", width:"72%"}}>
                            <Description description={"I like to consider myself an optimistic, focused and responsible person in my job. I love helping people discover efficient solutions to their problems."} />
                        </CardText>
                        <hr/>
                        <Mybutton onClick={this.changeMytitle}/>
                        <Mybutton2 onClick={this.changeMyimage}/>
                    </CardBody>
                </Card>
            </div>
        </div>

        );
    }
}


